$prefix: '.rise-blocks';

/* Grid */
#{$prefix}-block-lists {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

/* Icon Alignment style */
#{$prefix}-left-layout {
    #{$prefix}-content {
        padding-left: 30px;
    }
}

#{$prefix}-right-layout {
    #{$prefix}-content {
        padding-right: 30px;
    }
}

/* Alignment */
%common-alignment-style {
    display: flex;
}

#{$prefix}-left-layout {
    #{$prefix}-icon-box {
        @extend %common-alignment-style;

        #{$prefix}-content {
            flex-grow: 1;
        }

    }
}

#{$prefix}-right-layout {
    #{$prefix}-icon-box {
        @extend %common-alignment-style;

        #{$prefix}-icon-wrapper {
            order: 2;
        }

        #{$prefix}-content {
            flex-grow: 1;
        }

    }
}


/* =============================
 *  Save Function Style
 * ============================= */

/* Shadow */
#{$prefix}-shadow-enable {
    &#{$prefix}-save {
        #{$prefix}-block-lists {
            >div {
                box-shadow: 0px 0 16px 0 rgba(0, 0, 0, 0.05);
            }
        }
    }
}



.border-between-dashed .rise-blocks-block-lists>div:after {
    content: "";
    position: absolute;
    right: 0;
    height: 2px;
    width: 80px;
    border-top: 3px dashed #010834;
    top: 60px;
    transform: translate(64%);
}

.border-between-solid .rise-blocks-block-lists>div:after {
    content: "";
    position: absolute;
    right: 0;
    height: 2px;
    width: 80px;
    border-top: 3px solid #010834;
    top: 30%;
    transform: translate(66%);
}

.rise-blocks-block-lists>div {
    position: relative;
}

.rise-blocks-block-lists>div:last-child:after {
    display: none;
}

.rise-blocks-icon-box .wrapper-link {
    position: absolute;
    height: 100%;
    width: 100%;
    display: block;
    left: 0;
    right: 0;
}